.relatedImagesContainer {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0 10px; /* Th√™m padding ƒë·ªÉ tr√°nh b·ªã tr√†n vi·ªÅn */
}

.title {
    font-size: 24px;
    color: #2c3e50;
    font-weight: bold;
    margin-bottom: 15px;
}

.imageWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.imageList {
    display: flex;
    gap: 15px;
    overflow: hidden;
}

/* ƒêi·ªÅu ch·ªânh k√≠ch th∆∞·ªõc ·∫£nh theo m√†n h√¨nh */
.imageItem {
    width: 450px;
    height: 300px;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.imageItem img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
}

.imageItem:hover img {
    transform: scale(1.1);
}

/* N√∫t ƒëi·ªÅu h∆∞·ªõng */
.navButton {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 50%;
    font-size: 18px;
    transition: background-color 0.3s ease-in-out;
}

.navButton:hover {
    background-color: #0056b3;
}

/* ·∫®n n√∫t khi kh√¥ng th·ªÉ b·∫•m */
.navButton.disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* ‚úÖ Responsive cho Tablet (M√†n h√¨nh nh·ªè h∆°n 1024px) */
@media (max-width: 1024px) {
    .imageItem {
        width: 350px;
        height: 250px;
    }
}

/* ‚úÖ Responsive cho Mobile (M√†n h√¨nh nh·ªè h∆°n 768px) */
@media (max-width: 768px) {
    .imageWrapper {
        flex-direction: column;
        align-items: center;
    }

    .imageList {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    .imageItem {
        width: 90%; /* Chi·∫øm g·∫ßn h·∫øt m√†n h√¨nh ƒë·ªÉ d·ªÖ nh√¨n */
        height: auto;
    }

    .navButton {
        display: none; /* ·∫®n n√∫t ƒëi·ªÅu h∆∞·ªõng tr√™n m√†n h√¨nh nh·ªè */
    }
}

/* ‚úÖ Responsive cho Mobile r·∫•t nh·ªè (M√†n h√¨nh nh·ªè h∆°n 480px) */
@media (max-width: 480px) {
