/* To√†n b·ªô trang */
.productPage {
    padding: 20px;
    max-width: 1200px;
    margin: auto;
}

/* Ti√™u ƒë·ªÅ danh s√°ch s·∫£n ph·∫©m */
.sectionTitle {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin: 20px 0;
    color: #333;
    background: linear-gradient(to right, #ff9966, #ff5e62);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: fadeIn 1s ease-in-out;
}

/* Container danh s√°ch s·∫£n ph·∫©m */
.productContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

/* Card s·∫£n ph·∫©m */
.productCard {
    background: white;
    width: 220px;
    padding: 15px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
}

.productCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

/* ·∫¢nh s·∫£n ph·∫©m */
.productImage {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 8px;
}

/* T√™n s·∫£n ph·∫©m */
.productName {
    font-size: 18px;
    font-weight: bold;
    margin: 10px 0;
    color: #444;
}

/* Gi√° s·∫£n ph·∫©m */
.productPrice {
    font-size: 16px;
    color: #d9534f;
    font-weight: bold;
}

/* Th√¥ng b√°o khi kh√¥ng c√≥ s·∫£n ph·∫©m */
.noProduct {
    font-size: 18px;
    color: gray;
    text-align: center;
    width: 100%;
    margin-top: 20px;
}

/* Hi·ªáu ·ª©ng xu·∫•t hi·ªán */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ‚úÖ Responsive cho Tablet (768px - 1024px) */
@media (max-width: 1024px) {
    .productContainer {
        gap: 15px;
    }

    .productCard {
        width: 180px; /* Nh·ªè h∆°n ƒë·ªÉ ph√π h·ª£p v·ªõi m√†n h√¨nh nh·ªè h∆°n */
    }

    .productImage {
        height: 150px;
    }

    .productName {
        font-size: 16px;
    }

    .productPrice {
        font-size: 14px;
    }
}

/* ‚úÖ Responsive cho Mobile (d∆∞·ªõi 768px) */
@media (max-width: 768px) {
    .productContainer {
        flex-direction: column;
        align-items: center;
    }

    .productCard {
        width: 90%;
        max-width: 350px; /* Gi·ªõi h·∫°n k√≠ch th∆∞·ªõc ƒë·ªÉ kh√¥ng qu√° r·ªông */
    }

    .productImage {
        height: 200px;
    }

    .productName {
        font-size: 20px;
    }

    .productPrice {
        font-size: 16px;
    }
}

/* ‚úÖ Responsive cho ƒëi·ªán tho·∫°i nh·ªè h∆°n 414px (iPhone SE, Galaxy Fold) */
@media (max-width: 414px) {
    .productCard {
        width: 100%;
    }

    .productImage {
        height: 180px;
    }

    .productName {
        font-size: 18px;
    }

    .productPrice {
        font-size: 14px;
    }
}
