.bestSellerSection {
    margin-bottom: 30px;
    padding: 20px;
    background-color: #f8f8f8;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Ti√™u ƒë·ªÅ */
.sectionTitle {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
    text-align: center;
}

/* Ch·ª©a c√°c s·∫£n ph·∫©m - c√≥ th·ªÉ cu·ªôn ngang */
.productContainer {
    display: flex;
    gap: 15px;
    overflow-x: auto;
    padding-bottom: 10px;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
}

/* Hi·ªáu ·ª©ng m∆∞·ª£t khi cu·ªôn */
.productContainer::-webkit-scrollbar {
    height: 8px;
}

.productContainer::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 10px;
}

/* M·ªói s·∫£n ph·∫©m */
.productCard {
    flex: 0 0 200px;
    background: white;
    padding: 15px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
    scroll-snap-align: center;
}

.productCard:hover {
    transform: scale(1.05);
}

/* ·∫¢nh s·∫£n ph·∫©m */
.productImage {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 10px;
}

/* T√™n s·∫£n ph·∫©m */
.productName {
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
}

/* Gi√° s·∫£n ph·∫©m */
.productPrice {
    font-size: 14px;
    color: #d9534f;
}

/* ‚úÖ Responsive cho Tablet (d∆∞·ªõi 1024px) */
@media (max-width: 1024px) {
    .sectionTitle {
        font-size: 22px;
    }

    .productContainer {
        gap: 10px;
        padding-bottom: 8px;
    }

    .productCard {
        flex: 0 0 180px;
    }
}

/* ‚úÖ Responsive cho Mobile (d∆∞·ªõi 768px) */
@media (max-width: 768px) {
    .bestSellerSection {
        padding: 15px;
    }

    .sectionTitle {
        font-size: 20px;
    }

    .productContainer {
        gap: 8px;
        padding-bottom: 6px;
    }

    .productCard {
        flex: 0 0 160px;
        padding: 10px;
    }

    .productImage {
        height: 130px;
    }

    .productName {
        font-size: 14px;
    }

    .productPrice {
        font-size: 13px;
    }
}

/* ‚úÖ Responsive cho ƒëi·ªán tho·∫°i nh·ªè h∆°n 414px (iPhone SE, Galaxy Fold) */
@media (max-width: 414px) {
    .sectionTitle {
        font-size: 18px;
    }

    .productContainer {
        gap: 5px;
    }

    .productCard {
        flex: 0 0 140px;
        padding: 8px;
    }

    .productImage {
        height: 120px;
    }

    .productName {
        font-size: 13px;
    }

    .productPrice {
        font-size: 12px;
    }
}
